<template>
  <div class="introduce-container vip_container">
    <introduce-page-title :index="2"></introduce-page-title>
    <div class="content-box">
      <div class="download_box">
        <div class="download_title">立即下载</div>
        <div class="download_list">
          <div class="download_item">
            <div class="download_item_nohover">
              <img src="../../images/download_android.png" alt="">
              <div>Android</div>
            </div>
            <div class="download_item_hover">
              <img :src="android" alt="">
              <div>扫描二维码</div>
              <div>下载手机客户端</div>
            </div>
          </div>
          <div class="download_item">
            <div class="download_item_nohover">
              <img src="../../images/download_ios.png" alt="">
              <div>iPhone</div>
            </div>
            <div class="download_item_hover">
              <img :src="ios" alt="">
              <div>扫描二维码</div>
              <div>下载手机客户端</div>
            </div>
          </div>
        </div>
      </div>
      <introduce-page-bottom></introduce-page-bottom>
      <introduce-page-bottom1></introduce-page-bottom1>
    </div>
  </div>
</template>
<script>
import {
  latestApp
} from '../../config/api'
import introducePageTitle from './introducePageTitle.vue' //顶部流程模块
import introducePageBottom from './introducePageBottom.vue' //顶部流程模块
import introducePageBottom1 from './introducePageBottom1.vue' //顶部流程模块
export default {
  data() {
    return {
      android:'',
      ios:''
    }
  },
  components: { introducePageTitle, introducePageBottom, introducePageBottom1 },
  created() {
    this.initData();
  },
  mounted() {},
  computed: {

  },
  methods: {
    initData(){
      latestApp().then(res=>{
        if(res.data.msgNo==1){
          this.android = res.data.payload.android.path;
          this.ios = res.data.payload.ios.path;
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
.introduce-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  overflow: auto;
  .content-box {
    padding-top: 84px;
  }
  .download_box {
    text-align: center;
    margin-top: 84px;
    .download_title {
      font-family: "PingFang SC";
      font-weight: 300;
      color: #35383A;
      font-size: 42px;
      line-height: 1em;
    }
    .download_list {
      display: flex;
      margin-top: 40px;
      justify-content: center;
      padding-bottom: 100px;
    }
    .download_item {
      width: 320px;
      height: 390px;
      background: #fff;
      box-shadow: 0px 2px 24px 0px rgba(229, 229, 229, 0.5);
      border-radius: 2px;
      border: 1px solid rgba(241, 241, 241, 1);
      margin: 0px 80px;
      position: relative;
      &:hover {
        .download_item_hover {
          opacity: 1;
          margin-top: 0px;
        }
        .download_item_nohover {
          display: none;
        }
      }
      .download_item_hover {
        //display: none;
        transition:all 0.3s;
        position: absolute;
        margin-top: 10px;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        opacity: 0;
        img {
          width: 278px;
          height: 278px;
          margin-top: 28px;
          margin-bottom: 10px;
        }
        div{
          font-size: 18px;
          color: #A0A4AA;
          line-height: 26px;
        }
      }
      .download_item_nohover {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 0px;
        bottom: 0px;
        //transition:all 0.3s;
        img {
          width: 150px;
          height: 150px;
          margin: 76px 0px 64px;
        }
        div {
          color: #35383A;
          font-size: 30px;
        }
      }
    }
  }
}

</style>



